import React from 'react';
import { Trans } from 'react-i18next';

const Paragraphs = ({ components, lines, margin }) => {
  let slotsCount = -1;
  return (
    <React.Fragment>
      {lines.map((text, index) => {
        if (text === '') {
          return <br />;
        }
        if (text === '[slot]') {
          slotsCount += 1;
          return components[slotsCount];
        }
        return (
          <p
            key={index}
            className={margin ? '' : 'no-margin'}
            dangerouslySetInnerHTML={{ __html: text }}
          />
        );
      })}
    </React.Fragment>
  );
};

export default Paragraphs;
