import React from 'react';
import { Trans } from 'react-i18next';
import { Link, withIntl } from '../i18n';
import PageWrapper from '../components/PageWrapper';
import Paragraphs from '../components/Paragraphs';
import { ContentTitle, PageTitle, Wysiwyg } from '../ui/utilities';

const Legal = ({ t, ...context }) => {
  const phone = `<a href="tel:${t('contact.phoneNumber')}">${t('contact.phone')}</a>`;
  const email = `<a href="mailto:${t('contact.email')}">${t('contact.email')}</a>`;

  const sections = t('legal.sections', {
    phone,
    email,
  });

  return (
    <PageWrapper title={t('legal.title')} reader {...context}>
      <PageTitle>{t('legal.title')}</PageTitle>
      <ContentTitle className="t-h1" aria-hidden="true">{t('legal.title')}</ContentTitle>
      <Wysiwyg>
        <Paragraphs lines={t('legal.description')} margin />
        {sections.map((section, index) => (
          <React.Fragment key={`legal_section${index}`}>
            <h2>{section.title}</h2>
            <Paragraphs lines={section.description} margin />
          </React.Fragment>
        ))}
        <p>
          {t('legal.cookiesLink')}{' '}
          <Link to={`/${t('routes.cookies')}`}>{t('commons.moreInfo')}</Link>
        </p>
      </Wysiwyg>
    </PageWrapper>
  );
};

export default withIntl()(Legal);
